import { Component, OnInit } from "@angular/core";
import { DatePipe } from "@angular/common";
import { HttpErrorResponse } from "@angular/common/http";
import * as $ from "jquery";
import { CreateQuoteService } from 'src/app/core/services/create-quote-service';
import { CommonService } from 'src/app/core/services/common-service';
import { FileUploadService } from 'src/app/core/services/file-upload.service'
@Component({
  // selector: 'app-create-quote',
  templateUrl: "./file-upload.component.html",
  styleUrls: ["./file-upload.component.css"],
})
export class FileUploadComponent implements OnInit {
  lobProductList: any;
  token: any;
  lobList: any;
  lobCode: string;
  fileToUpload: File = null;
  msg: string="No file chosen";
  sucesserror: string="";
  uploaddata: any;
  response : any;
  constructor(
    public datepipe: DatePipe,
    private createQuoteService: CreateQuoteService, 
    private commonService: CommonService,
    private fileuploadService: FileUploadService)
    {
    }

  ngOnInit()
  {
    this.getuploadedData();
  }

  handleFileInput(files: FileList) 
  {
    this.fileToUpload = files.item(0);
    this.msg = this.fileToUpload.name;
  }

  excelUpload()
  {
    if(this.msg == "No file chosen")
    {
      this.sucesserror = 'Please select excel file for upload';
      $("#AlertModal").show();
    }
    var fileExt = this.fileToUpload.name.substr(this.fileToUpload.name.lastIndexOf('.') + 1);
    if(fileExt != "xlsx")
    {
      this.msg ="No file chosen";
      this.sucesserror = 'Please upload excel file';
      $("#AlertModal").show();
    }
    let formData = new FormData();
    formData.append('file', this.fileToUpload);
    this.fileuploadService
    .uploadFile(formData)
    .subscribe(
    (res) => 
    {
      console.log(res);
      if(res != "")
      {
        this.response = res;
        this.msg ="No file chosen";
        this.getuploadedData();
        this.sucesserror = this.response.message;
        $("#AlertModal").show();
      }
    },
    (error) =>
    {
      this.sucesserror ='There is some problem for uploading the sheet. Please contact to Administrator';
      $("#AlertModal").show();
    }
  );
  }

  getuploadedData()
  {
    this.fileuploadService
    .getuploadedData()
    .subscribe(
    (res) => 
    {
      this.uploaddata = res;
    },
    (error) => 
    {
      this.sucesserror ='There is some problem for getting the uploaded data. Please contact to Administrator';
      $("#AlertModal").show();
    });
  }
}
